.button {
  display: flex;
  justify-content: center;
  align-items: center;

  box-sizing: border-box;
  cursor: pointer;
  width: 100%;
  height: 56px;
  background-color: $mainColor;
  border: none;
  border-radius: 28px;

  font-size: 16px;
  font-weight: 700;
  text-decoration: none;
  color: $fontColorWhite;

  transition: color, background-color $animationDuration;

  &:hover {
    background-color: $fontColorWhite;
    color: $mainColor;
    border: 3px solid $mainColor;
  }

  &:active {
    background-color: $buttonActive;
    color: $fontColorWhite;
    border: 3px solid $buttonActive;
  }
}
